import Global from "../components/Global";
import "../../scss/pages/dashboard.scss";
import echarts  from "echarts";
import DataTableZ from "../components/dataTable";

(() => {

    let sysInfoApi = "/api/BackCmsHome/GetSysInfo";
    let chartsApi = "/api/BackCmsHome/GetChartData";
    let statisNumApi = "/api/BackCmsHome/GetStatisticsNum";
    let ipLogDataApi = "/api/BackCmsHome/GetIpLogData";

    const getSysInfo = ()=> {

        //#region 操作系统信息
        const parseTime = (ms: number) => {
            let usedCpuTime = ms / 1000;//秒
            let usedCpuTimeStrArr = [];
            if (usedCpuTime > 60) {
                let minute = Math.floor(usedCpuTime / 60);
                let reSeconds = usedCpuTime - minute * 60;
                usedCpuTimeStrArr.push(reSeconds.toFixed(0) + "s");
                if (minute > 60) {
                    let hour = Math.floor(minute / 60);
                    let reMinutes = minute - hour * 60;
                    usedCpuTimeStrArr.push(reMinutes.toFixed(0) + "m");
                    if (hour > 24) {
                        let day = Math.floor(hour / 24);
                        usedCpuTimeStrArr.push(day.toFixed(0) + "d");
                    } else {
                        usedCpuTimeStrArr.push(hour + "h");
                    }
                } else {
                    usedCpuTimeStrArr.push(minute + "m");
                }
            } else {
                usedCpuTimeStrArr.push(usedCpuTime.toFixed(0) + "s");
            }
            return usedCpuTimeStrArr.reverse().join(" ");
        }

        function getSysInfoMain() {
            Global.showLoading();
            $.get(sysInfoApi,
                msg => {
                    //{"UsedMem":120188,"UsedCPUTime":5593.75,"SysKernel":"Microsoft Windows 10.0.18362","SysArchitecture":"X64","SysRunTickCount":33788187}
                    let $sysInfo = $("#sysInfo");
                    $('.sysKernel', $sysInfo).html(msg.SysKernel + "(" + msg.SysArchitecture + ")");
                    $('.sysRunTickCount', $sysInfo).html(parseTime(msg.SysRunTickCount));

                    $('.usedMem', $sysInfo).html((msg.UsedMem / 1024.0).toFixed(2) + "MB");
                    $('.usedCPUTime', $sysInfo).html(parseTime(msg.UsedCPUTime));


                    Global.hideLoading();
                });
        }
        //#endregion

        getSysInfoMain();
    }

    const getChart = () => {
        // #region 日访问量
        let dom = <HTMLDivElement>document.getElementById('dayChart');
        var realChart = echarts.init(dom);
        var realChartOption:any = {
            title: {
                text: '网站日访问量',
                left: 'center'
            },
            tooltip: {
                trigger: 'axis'
            },
            dataZoom: [
                {
                    type: 'slider', //支持鼠标滚轮缩放
                    start: 0, //默认数据初始缩放范围为10%到90%
                    end: 100
                },
                {
                    type: 'inside', //支持单独的滑动条缩放
                    start: 0,
                    end: 100
                }
            ],
            color: [
                '#FF3333' //曲线颜色		  
            ],
            toolbox: {
                right: '32px',
                show: true,
                feature: {
                    dataView: { show: true },
                    restore: {},
                    saveAsImage: {
                        show: true,
                        excludeComponents: ['toolbox'],
                        pixelRatio: 2
                    }
                }
            },
            xAxis: {
                data: []
            },
            yAxis: [
                {
                    type: 'value',
                    name: '访问量'
                    //max: 1,
                    //min: 0
                }
            ],
            grid: {
                left: '6%',
                right: '10%',
                bottom: '15%'
            },
            series: [
                {
                    name: '访问量',
                    type: 'line',
                    symbol: 'emptycircle', //标识符号为实心圆
                    data: []
                }
            ]
        };// 指定图表的配置项和数据
        realChart.showLoading(); //数据加载完之前先显示一段简单的loading动画
        realChart.setOption(realChartOption);

        var loadDailyCountData = (chartData:any) => {
            var dates = [];
            var prob = [];
            for (var i = 0; i < chartData.length; i++) {
                var arr = chartData[i];
                dates.push(arr.LogDate); //replace(/-/g, '/')
                prob.push(arr.AccessCount);
            }

            realChart.hideLoading(); //隐藏加载动画
            realChart.setOption({
                xAxis: {
                    data: dates
                },
                series: [
                    {
                        // 根据名字对应到相应的系列
                        name: '访问量',
                        data: prob
                    }
                ]
            });
        }
        // #endregion

        // #region 用户访问地点统计
        var cityChart = echarts.init(<HTMLDivElement>document.getElementById('cityChart'));
        cityChart.showLoading();
        var loadCityCountData = (data:any) => {
            var len = data.length;
            len = data.length < 30 ? data.length : 30;
            var legendData = [];
            var seriesData = [];
            for (var i = 0; i < len; i++) {
                var name = data[i].City;
                var value = data[i].Count;
                legendData.push(name);

                seriesData.push({
                    name: name,
                    value: value
                });
            }

            cityChart.hideLoading(); //隐藏加载动画

            var option:any = {
                title: {
                    text: '来访地点统计',
                    x: 'center'
                },
                tooltip: {
                    trigger: 'item',
                    formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                toolbox: {
                    right: '32px',
                    show: true,
                    feature: {
                        dataView: { show: true },
                        restore: {},
                        saveAsImage: {
                            show: true,
                            excludeComponents: ['toolbox'],
                            pixelRatio: 2
                        }
                    }
                },
                legend: {
                    type: 'scroll',
                    orient: 'vertical',
                    right: 10,
                    top: 20,
                    bottom: 20,
                    data: legendData
                },
                series: [
                    {
                        name: '城市',
                        type: 'pie',
                        radius: "55%",
                        center: ['40%', '65%'],
                        data: seriesData,
                        itemStyle: {
                            emphasis: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        }
                    }
                ]
            };

            cityChart.setOption(option);
        }

        // #endregion

        $.ajax({
            url: chartsApi,
            type: "get",
            async: true,
            complete: function () { },
            success: function (msg) {
                //AccessBowserCounts
                loadDailyCountData(msg.Data.DailyAccess);
                loadCityCountData(msg.Data.AccessCityCounts);
            }
        });

    }

    const getStatisNum = () => {
        $.get(statisNumApi,
            (msg: any) => {
                if (msg.Code === 0) {
                    let data = msg.Data;
                    let $statisticsNum = $("#statisticsNum");
                    $("#allVisitor", $statisticsNum).text(data.AllVisitor);
                    $("#weekVisitor", $statisticsNum).text(data.WeekVisitor);
                    $("#yesterdayVisitor", $statisticsNum).text(data.YesterdayVisitor);
                    $("#unreadSiteMsg", $statisticsNum).text(data.UnreadSiteMsg);
                    $("#unreadEssayComment", $statisticsNum).text(data.UnreadEssayComment);
                } else {
                    Global.alertError(msg.Error);
                }
            });
    }

    const initTable = ()=>{
        let dt = new DataTableZ();
        let dtZConfig = {
            selector: "#dataTable",
            order: [[1,"desc"]],
            columns: [
                //DataTableZ.checkBoxCol(),//0
                {
                    "data": "AccessDate", "title": "时间",
                    width: "300px",
                    render: (data: any, type: any, row: any, meta: any) => {
                        return data;
                    }
                },
                {
                    "data": "Ip", "title": "IP",                   
                },
                {
                    "data": "Addr", "title": "地址",                   
                }
                // ,{
                //     "data": "Id", "title": "操作",
                //     render: (data: any, type: any, row: any, meta: any) => {
                //         return `<div class="btn-group ">
                //         <a class="btn btn-secondary btn-sm" href="/BackCms/Essays/Edit?Id=${data}">修改</a>
                //         <button class="btn btn-danger btn-sm row-del" data-id="${data}">删除</button>                    
                //     </div>`;
                //     }
                // }
            ],
            ajaxUrl: ipLogDataApi,
        };

        dt.render(dtZConfig);


    }

    getSysInfo();
    getChart();
    getStatisNum();
    initTable();




})();
